<template>
  <div class="header">
    <ul class="header-button-left">
      <li v-if="step == 1 || step==2" @click="step=0">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">Upload</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>
  <Container @setText="uploadText = $event" :selectedFilter="selectedFilter" :step="step" :uploadedImg="uploadedImg" />
  <button v-if="step==0" @click="$store.dispatch('getMore')">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        accept="image/*"
        type="file"
        id="file"
        class="inputfile"
      />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

  <!-- 
  <div v-if="tab==0">내용0</div>
  <div v-if="tab==1">내용1</div>
  <div v-if="tab==2">내용2</div>
  <button @click="tab=0">버튼0</button>
  <button @click="tab=1">버튼1</button>
  <button @click="tab=2">버튼2</button> -->
</template>

<script>
import Container from "./components/Container.vue";
import {mapState} from 'vuex'

export default {
  name: "App",
  data() {
    return {
      step: 0,
      uploadedImg: "",
      uploadText:"",
      selectedFilter:"",
      // tab:0,
    };
  },
  mounted(){
    this.emitter.on('selectedFilter',(a)=>{
      this.selectedFilter=a;
    });
  },
  computed:{
    ...mapState(['name','data'])
  }
  ,methods: {
    upload(e) {
      let pic = e.target.files;
      this.uploadedImg = URL.createObjectURL(pic[0]);
      this.step = 1;
    },
    publish() {
      var post = {
        name: "User",
        userImage: "https://placeimg.com/100/100/tech",
        postImage: this.uploadedImg,
        likes: 0,
        date: "Jan 21",
        liked: false,
        content: this.uploadText,
        filter: this.selectedFilter,
      };
      this.$store.commit('addPost',post);
      this.step=0;
    },
  },
  components: {
    Container,
  },
};
</script>

