import { createStore } from 'vuex'
import data from "./data.js"
import axios from "axios"

const store = createStore({
  state() {
    return {
      name: 'jamie',
      data: data,
      moreCount:0,
    }
  },
  mutations: {
    changeName(state) {
      state.name = 'park';
    },
    addAge(state, payload) {
      state.age += payload;
    },
    like(state, payload) {
      state.data[payload].likes += state.data[payload].liked ? -1 : 1;
      state.data[payload].liked = !state.data[payload].liked
    },
    addPost(state, payload) {
      state.data.unshift(payload);
    },
    setMore(state,payload){
      state.data.push(payload);
      state.moreCount++;
    },
    resetMore(state){
      state.moreCount=0;
    }
  },
  actions: {
    getMore(context) {
      if(context.state.moreCount>1){
        context.commit('resetMore');
      }
      axios.get(`https://codingapple1.github.io/vue/more${context.state.moreCount}.json`)
        .then((a) => {
          context.commit('setMore',a.data);
        })
        .catch((err)=>{
          console.log(err);
        }
        )
    },
  },
})

export default store
