<template>
  <div class="post">
    <div class="post-header">
      <div
        class="profile"
        :style="`background-image: url(${post.userImage});`"
      ></div>
      <span class="profile-name">{{ post.name }}</span>
    </div>
    <div
      @dblclick="like"
      class="post-body"
      :class="post.filter"
      :style="`background-image: url(${post.postImage});`"
      onselectstart="return false"
    >
      <Transition name="Animation">
        <img
          v-if="likeAnimation && post.liked"
          class="likeAnimation"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/4/44/Coraz%C3%B3n.svg/1200px-Coraz%C3%B3n.svg.png"
          style="width:80px; height:80px;"
        />
      </Transition>
      <Transition name="Animation">
        <img
          v-if="likeAnimation && !post.liked"
          class="likeAnimation"
          src="http://www.clker.com/cliparts/l/O/F/M/b/m/small-red-heart-black-and-white-only-md.png"
          style="width:80px; height:80px;"
        />
      </Transition>
    </div>
    <div class="post-content">
      <p>
        <img
          v-if="!post.liked"
          src="http://www.clker.com/cliparts/l/O/F/M/b/m/small-red-heart-black-and-white-only-md.png"
          style="width: 10px; height: 10px"
        />

        <img
          v-if="post.liked"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/4/44/Coraz%C3%B3n.svg/1200px-Coraz%C3%B3n.svg.png"
          style="width: 10px; height: 10px"
          
        />
        {{ post.likes }} Likes
      </p>
      <p>
        <strong>{{ post.name }}</strong> {{ post.content }}
      </p>
      <p class="date">{{ post.date }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "postComponent",
  data() {
    return {
      likeAnimation: false,
    };
  },
  props: {
    post: Object,
    postId: Number,
  },
  methods: {
    like() {
      this.$store.commit("like", this.postId);
      this.likeAnimation = true;
      setTimeout(() => {
        this.likeAnimation = false;
      }, 600);
    },
  },
};
</script>

<style>


.likeAnimation {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.post {
  width: 100%;
}
.profile {
  width: 30px;
  height: 30px;
  background-size: 100%;
  border-radius: 50%;
  float: left;
}
.profile-name {
  display: block;
  float: left;
  padding-left: 10px;
  padding-top: 7px;
  font-size: 14px;
}
.post-header {
  height: 30px;
  padding: 10px;
}
.post-body {
  height: 450px;
  background-position: center;
  background-size: cover;
}
.post-content {
  padding-left: 15px;
  padding-right: 15px;
  font-size: 14px;
}
.date {
  font-size: 11px;
  color: grey;
  margin-top: -8px;
}
.Animation-enter-from, .Animation-leave-to{
  width:0 !important;
  height:0 !important;
}
.Animation-enter-active{
  transition: all 0.1s;
}
.Animation-leave-active{
  transition: all 0.2s;
}
.Animation-enter-to, .Animiation-leave-from{
  width:80px !important;
  height:80px !important;
}
</style>